<template>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <div class='whole-thing'></div>
      <label>Task</label>
      <input type="text" v-model="label" name="text" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Day & Time</label>
      <input
        type="date"
        name="day"
        v-model="day"
        placeholder= "Any Day & Time"
      />
      <p class="para">{{Current_date()}} by default</p>
    </div>
    <div class="form-control form-control-check">
      <label>Set Reminder</label>
      <input type="checkbox" v-model="reminder" name="reminder" />
    </div>

    <input type="submit" value="Save Task" :class="[label ?'btn1' : 'btn' ,'btn-block']" />
  </form>

</template>

<script >
import db from '@/fb.js'
import firebase from 'firebase/app'
import 'firebase/auth'
export default{
    name: 'AddTask',
    data (){
        return{
            label: "",
            day: "",
            reminder : true
        }
    },
    emits: ["ToggleAdd"],
    props:{
      showAdd: Boolean
    },
    methods: {
        pushing_to_db(collection, newTask){
          db.collection(collection).add(newTask).then(() => {
          
          })
        },
        Current_date(){
            const current = new Date();
            const date = `${current.getDate()}-${current.getMonth()+1}-${current.getFullYear()}`;
            return date;
        },
        onSubmit(e){
            e.preventDefault();
            if(!this.label){
                alert("Please add task");
                return;
            }
            if (this.day == ''){
                const current = new Date();
                const date = `${current.getDate()}-${current.getMonth()+1}-${current.getFullYear()}`;
                this.day = date;
            }
            const newTask = {
                text: this.label,
                day: this.day,
                reminder: this.reminder,
            }
            // submiting the new task on public or user uid
            firebase.auth().onAuthStateChanged((user) => {
              if (user){
                this.pushing_to_db(user.uid, newTask);
                console.log("Added to personal DB")
              }
              else{
                this.pushing("public", newTask);
                console.log("Added to public DB")
              }
            })
            this.$emit('add-Task', newTask)
            this.$emit('ToggleAdd')
            
            this.label = '';
            this.day = '';
            this.reminder = true;
        }
    },
}
</script>

